<template>
  <div>
    <el-row>
      <el-col :xs="4" :sm="12" :md="4" :lg="6" :xl="6">
        <div class="card2">
          <el-button type="success send" @click="WebSocketByte">WebSocketByte</el-button>
          <br><br>
          <el-button type="danger send" @click="SpringNettyByte">SpringNettyByte</el-button>
        </div>
      </el-col>
      <el-col :xs="12" :sm="12" :md="12" :lg="18" :xl="18">
        <chat-page :content-list="chatBox" :my="my" :it="it"></chat-page>
      </el-col>

    </el-row>
  </div>
</template>

<script>
    import chatPage from './chatPage';
    import msg from '@/proto/CommonMsgPB_pb';

    export default {
        data() {
            return {

                my: '',
                it: '',
                msg: '',
                items: ["3", 666],
                mp: new Map(),
                meindex: new Map(),
                userList: [],
                chatBox: [],
                messageList: [],
                sid: ''
            }
        },
        components: {
            chatPage,
        },
        mounted() {
        },
        methods: {
            WebSocketByte() {
                var wsuri = "ws://127.0.0.1:8084/test/gameServer";
                this.initSocket(wsuri);
            },
            SpringNettyByte() {
                var wsuri = "ws://127.0.0.1:5000/websocket";
                this.initSocket(wsuri);
            },
            initSocket(wsuri) {
                this.socketApi.initWebSocket(wsuri);
                this.my = 1;
                this.socketApi.setController(this.controller);
                this.socketApi.setdisConnection(this.disconnect);
                this.getFriendsList();

            },
            controller(e) {
                let bb = new msg.CommonMsgPB.deserializeBinary(e);
                console.log("接受消息:"+bb);

            },
            getFriendsList() {

            },
            disconnect() {
            },
            analyzeOneMessage(m) {
                let me = new Object();
                me.fromId = m.getFromid();
                me.toId = m.getToid();
                me.content = m.getContent();
                me.time = m.getTime();
                me.type = m.getContenttype();
                let id = me.fromId;
                if (id == this.my.uid)
                    id = me.toId
                let list = this.mp.get(id);
                console.log(list);
                list.push(me);
                let index = this.meindex.get(id);
                this.messageList[index] = me;
            },


            toggle(user) {
                this.sid = user.id;
                let index = user.id;
                this.it = user;
                this.chatBox = this.mp.get(index);
            }
        }
    }
</script>

<style scoped>
  .card2 {
    height: 610px;
    overflow: auto;
  }
</style>



